import React, { Component } from "react"
import { Route, Switch, HashRouter } from "react-router-dom"
import Splash from "../pages/splash/Splash";
import Home from "../pages/home/HomeComponent"
import Experience from "../pages/experience/ExperienceComponent"
import Projects from "../pages/projects/ProjectComponent"
import API from "../pages/apis/ApiComponent"
import CheatSheet from "../pages/cheatSheets/CheatSheetComponent"
import Blog from "../pages/blog/BlogComponent"
import Contact from "../pages/contact/ContactComponent"

export default class Main extends Component {
    render() {
        return (
            <div>
                <HashRouter basename="/">
                    <Switch>
                        <Route path="/" exact component={Splash} />
                        <Route path="/home" component={Home} />
                        <Route path="/experience" component={Experience} />
                        <Route path="/projects" component={Projects} />
                        <Route path="/api" component={API} />
                        <Route path="/cheat-sheets" component={CheatSheet} />
                        <Route path="/blog" component={Blog} />
                        <Route path="/contact" component={Contact} />
                    </Switch>
                </HashRouter>
            </div>
        )
    }
}